* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Ubuntu", sans-serif;
  text-align: center;
}

.light {
  transition: 0.3s;
  background-image: url(1.jpg);
}

.dark {
  transition: 0.3s;

  background-image: url(2.jpg);
}

.navbar {
  background-color: #075e54;
  min-height: 7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ece5dd;
  font-size: 1.7rem;
  box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}

.theme {
  position: absolute;
  right: 5rem;
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 15px;
}

.user-box-parent {
  display: flex;
  flex-wrap: wrap;
}

.user-box {
  height: 350px;
  margin: 8rem auto;
  flex: 0 0 40%;
  background-color: #075e54;
  border-radius: 10px;
  color: #ece5dd;
  transition: 0.2s;
  box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}

.user-box:hover {
  transform: scale(1.01);
}

.user-pic {
  height: 150px;
}

.user-img {
  margin: 10px;
  position: relative;
  top: 40px;
  border: 2px solid #ece5dd;
  outline: 2px solid #ece5dd;
  border-radius: 100px;
  height: 125px;
  width: 125px;
  z-index: 1;
  transition: 0.3s;
}

.user-img:hover {
  transform: scale(1.1);
}

p {
  margin: 20px;
}

.user-info {
  margin-top: 2rem;
}

.user-btn {
  display: flex;
  margin: 1.5rem;
  align-items: center;
  justify-content: space-around;
}

.user-info-btn {
  background: transparent;
  border: none;
  color: #ece5dd;
  font-size: 1.5rem;
  padding: 10px;
  transition: 0.3s;
  list-style: none;
}

.user-info-btn:hover {
  color: #25d366;
  transition: 0.3s;
  transform: scale(1.5);
}
